body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    background: #f6f8fa;
}

.App .headerWrapper {
    margin: 0px 0px 80px 0px;
}
.sl-header img {
    background: url('/images/logo.svg')
}
.sl-productOverview {
    text-align: center;
}

.sl-productOverview img {
    height: 200px;
    width: 200px;
}

.sl-productOverview h5 {
    font-weight: bold;
}
.sl-productTags div[role="button"] {
    height: 24px;
    margin: 5px 5px;
    border-radius: 4px;
}

.sl-salesChart .c3-line {
    stroke-width: 5px;
}

